"use client";

import { useEffect, useState } from "react";
import { getProductById, Product } from "@/lib/products";
import { useCart } from "@/context/CartContext";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { useRouter } from "next/navigation";
import Image from "next/image";

export default function ProductDetailPage({
  params,
}: {
  params: { id: string };
}) {
  const router = useRouter();
  const { dispatch } = useCart();
  const [product, setProduct] = useState<Product | null>(null);

  useEffect(() => {
    const foundProduct = getProductById(params.id);
    if (foundProduct) {
      setProduct(foundProduct);
    }
  }, [params.id]);

  if (!product) {
    return (
      <div className="container mx-auto px-4 py-16 text-center">
        <h1 className="text-2xl font-bold mb-4">Product Not Found</h1>
        <p className="text-gray-600 mb-8">The product you're looking for doesn't exist.</p>
        <Button
          onClick={() => router.push("/")}
          className="bg-black hover:bg-gray-800 text-white"
        >
          Return to Home
        </Button>
      </div>
    );
  }

  const addToCart = () => {
    dispatch({ type: "ADD_ITEM", product });
    router.push("/cart");
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        {/* Product Image */}
        <div className="relative aspect-square">
          <Image
            src={product.imageUrl}
            alt={product.name}
            fill
            className="object-cover rounded-lg"
            sizes="(max-width: 768px) 100vw, 50vw"
            priority
          />
        </div>

        {/* Product Details */}
        <div className="space-y-6">
          <div>
            <h1 className="text-3xl font-bold mb-2 font-playfair">{product.name}</h1>
            <p className="text-2xl font-semibold">${product.price.toFixed(2)}</p>
          </div>

          <p className="text-gray-600">{product.description}</p>

          {product.details && (
            <Card className="p-6">
              <h3 className="font-semibold mb-4">Product Details</h3>
              <ul className="list-disc list-inside space-y-2">
                {product.details.map((detail, index) => (
                  <li key={index} className="text-gray-600">{detail}</li>
                ))}
              </ul>
            </Card>
          )}

          <div className="space-y-4">
            <Button
              onClick={addToCart}
              className="w-full bg-black hover:bg-gray-800 text-white py-6"
            >
              Add to Cart
            </Button>
            
            <Button
              onClick={() => router.push(`/products/${product.category}`)}
              variant="outline"
              className="w-full py-6"
            >
              Back to {product.category === 'men' ? "Men's" : "Women's"} Collection
            </Button>
          </div>

          {/* Additional Information */}
          <div className="space-y-4 pt-6 border-t">
            <div>
              <h3 className="font-semibold mb-2">Free Shipping</h3>
              <p className="text-gray-600">Estimated delivery: 5-7 business days</p>
            </div>
            <div>
              <h3 className="font-semibold mb-2">Returns</h3>
              <p className="text-gray-600">Free returns within 30 days</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
